.App {
    text-align: center;
    background: linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.5) ), url('./images/olga-thelavart-1nrY9CLAGcI-unsplash.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    height: 150vh
  }
  
  .App h1 {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    margin-top: 0;
  }
  
  .title {
    font-size: 4vw;
    padding-top: 10px;
    color: white;
  }
  
  .navBar ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  .navBar li {
    display: inline;
    padding: 8px;
    background-color: #dddddd;
  }
  
  li:hover {
    background-color: #111;
  }
  
  .homepage {
    padding-top: 50px;
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .aboutpage {
    width: 75%;
    margin: 0 auto;
    margin-top: 25px;
    font-size: 1.3vw;
    font-weight: bold;
    color: white;
  }
  
  .aboutpage img {
    width: 50%;
    height: 50%;
    margin: 0 auto
  }
  
  .aboutpage h1 {
    border-bottom: 6px white solid;
    font-size: 5vh;
  }
  
  .packages {
    width: 75%;
    margin: 0 auto;
    margin-top: 50px;
    font-size: 1.3vw;
    font-weight: bold;
    color: white;
  }
  
  .packages h1 {
    border-bottom: 6px white solid;
    font-size: 5vh;
  }
  
  .packages ul {
    list-style-type: none;
    margin: 0 auto;
    padding: 0;
  }
  
  .packages li {
    font-size: 25px;
    margin: 20px;
  }